import React, { useState } from 'react';
import { Table, Button } from 'reactstrap';
import PropTypes from 'prop-types'
import confirm from "reactstrap-confirm";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMoneyBillAlt, faPrint } from "@fortawesome/free-solid-svg-icons";
import { printDiv } from "../../utils/helper";
import OrderDetail from './OrderDetail';
import Checkout from './Checkout'
import DataPagination from '../common/DataPagination';



const DataTable = ({ stateData: { isLoading, list, buyer, next, previous }, getData }) => {
    const [editData, setEditData] = useState({})
    const [modalOpen, setModalOpen] = useState(false)
    const [printItem, setPrintItem] = useState({})


    const paymentHandler = (item) => {
        setEditData(item)
        setModalOpen(true)
    }

    const printHandler = item => {
        setPrintItem(item)
        setInterval(() => {
            printDiv('order-detail-wrap')
        }, 500);
    }

    return (
        <>
            <Table responsive striped bordered hover size="sm" className="text-center order-history">
                <thead>
                    <tr>
                        <th> </th>
                        <th> </th>
                        <th> </th>
                        <th> </th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {isLoading ? <tr>
                        <td colSpan={10}>Loading...</td>
                    </tr> : list.length === 0 ? <tr><td colSpan='10'>   </td></tr> : list.map((item, i) => <tr key={item.id}>
                        <td>{i + 1}</td>
                        <td>{item.order.order_no}</td>
                        <td>{item.amount}</td>
                        <td>
                            {item.payments.length > 0 && item.payments.map(p => <p key={p.id}>{p.bank_name && `${p.bank_name} || ${p.branch_name} || ${p.check_no} || ${p.check_date} || ${p.received ? 'Received' : 'Pending'}`} || {p.attachment && <a href={p.attachment} target='_blank'>View</a>}</p>)}
                        </td>
                        <td>{item.status}</td>
                        <td>
                            {item.status === 'Unpaid' &&
                                <Button
                                    color="warning"
                                    size="sm"
                                    className="mr-2"
                                    title=' '
                                    onClick={async () => await confirm({ title: '  ', message: "      ?", confirmText: "", cancelText: "" }) && paymentHandler(item)}
                                >
                                    <FontAwesomeIcon icon={faMoneyBillAlt} />
                                </Button>}
                            <Button
                                color="dark"
                                size="sm"
                                className="mr-2"
                                title=' '
                                onClick={() => printHandler(item)}
                            >
                                <FontAwesomeIcon icon={faPrint} />
                            </Button>
                            {
                                Object.keys(printItem).length > 0 && <OrderDetail
                                    buyer={buyer}
                                    selectedOrder={printItem}
                                />
                            }

                        </td>
                    </tr>)}
                </tbody>
            </Table>

            <DataPagination
                pageHandler={getData}
                next={next}
                previous={previous}
            />

            {modalOpen &&
                <Checkout
                    data={editData}
                    getData={getData}
                    modalHandler={() => setModalOpen(false)}
                />}

        </>
    );
}

DataTable.propTypes = {
    stateData: PropTypes.object.isRequired,
    getData: PropTypes.func.isRequired,
}

export default DataTable;
